@import url('https://fonts.googleapis.com/css?family=Macondo');

body {
    margin:0;
    padding:0;
    font-family: sans-serif;
    /*font-family: Macondo,cursive;*/
}

.header {
    font-family: Macondo,cursive;
    font-weight: 400;
    padding-top: 10px;
    padding-bottom: 10px;
    color: #000;
    font-size: 40px;
}

.container {

    max-width: 420px;

}

.main {
    border: 2px solid #000;
    text-align: center;
    background-image: url(img/background.png);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: top;
    color: #000;
    margin: 0;
    padding: 0;
    min-height: 700px;
}

/* Provide sufficient contrast against white background */
a {
  color: #0366d6;
}

code {
  color: #E01A76;
}

.btn-primary {
  color: #fff;
  background-color: #1b6ec2;
  border-color: #1861ac;
}

.menuitem {
    background: #d1c99f;
    background-image: -webkit-linear-gradient(top, #d1c99f, #e6d565);
    background-image: -moz-linear-gradient(top, #d1c99f, #e6d565);
    background-image: -ms-linear-gradient(top, #d1c99f, #e6d565);
    background-image: -o-linear-gradient(top, #d1c99f, #e6d565);
    background-image: linear-gradient(to bottom, #d1c99f, #e6d565);
    -webkit-border-radius: 6;
    -moz-border-radius: 6;
    border-radius: 6px;
    text-shadow: 2px 2px 7px #666666;
    -webkit-box-shadow: 1px 1px 3px #575442;
    -moz-box-shadow: 1px 1px 3px #575442;
    box-shadow: 1px 1px 3px #575442;
    color: #ffffff;
    font-size: 20px;
    padding: 10px 20px 10px 20px;
    border: solid #a38968 2px;
    text-decoration: none;

    padding: 10px 20px 10px 20px;
    margin: 10px;
    width: 350px;
    display: inline-block;
}
.menuitem:hover {
    color: #666666;
    background: #d9d3b0;
    background-image: -webkit-linear-gradient(top, #d1c99f, #d9d3b0);
    background-image: -o-linear-gradient(top, #d1c99f, #d9d3b0);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#d1c99f), to(#d9d3b0));
    background-image: linear-gradient(to bottom, #d1c99f, #d9d3b0);
    text-decoration: none;
}


.button {
    background: #d1c99f;
    background-image: -webkit-linear-gradient(top, #d1c99f, #e6d565);
    background-image: -o-linear-gradient(top, #d1c99f, #e6d565);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#d1c99f), to(#e6d565));
    background-image: linear-gradient(to bottom, #d1c99f, #e6d565);
    border-radius: 6px;
    text-shadow: 2px 2px 7px #666666;
    -webkit-box-shadow: 1px 1px 3px #575442;
    box-shadow: 1px 1px 3px #575442;
    color: #000;
    font-size: 20px;
    padding: 10px 20px 10px 20px;
    border: solid #a38968 2px;
    text-decoration: none;
}